import { Component, OnInit } from '@angular/core';
import { ISubscription } from "rxjs/Subscription";
import { CommonService, Storage, AuthenticationService } from '../../services';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import config from '../../config';
declare const fabric: any;

@Component({
  selector: 'app-superuserhome',
  templateUrl: './superuserhome.component.html',
  styleUrls: ['./superuserhome.component.scss']
})
export class SuperuserhomeComponent implements OnInit {

  windowview: any;
  authlink = config.api.AuthCode;
  stopCondition: boolean = true;
  private subscription: ISubscription;
  dialogComponent: any;

  constructor(
    private storage: Storage,
    private router: Router,
    private authenticationService: AuthenticationService
  ) { }

  ngOnInit() {
    let user = this.storage.get('user');
    if (user && user.isSuperAdmin && user.isActive) {
      this.router.navigate([config.routes.superuserhome]);
    } else {
      this.storage.remove('user');
      this.authlink = this.authlink + config.api.redirecUrl;
      this.readCode();
      this.windowview = window.open(this.authlink, "_blank", "width=400,height=400");
    }
  }
  ngOnDestroy() {
    this.subscription &&
      this.subscription.unsubscribe();
  }

  readCode() {
    this.stopCondition = false;
    this.subscription = Observable.interval(2000)
      .takeWhile(() => !this.stopCondition)
      .subscribe(i => {
        let code = this.storage.get('code');
        if (code) {
          this.windowview.close();
          this.stopCondition = true; 
          this.authenticationService.getauthwithuserresponse(code).subscribe(data => {

            data.user.type = true;
            this.storage.set('user', data.user);
            if (data.user.type && data.user.isSuperAdmin && data.user.isActive) {
              this.router.navigate([config.routes.superuserhome]);
            } else {
              this.dialogComponent.open();
            }
          });
        }
      });
  }

  ngAfterViewInit() {
    this.fabricInitialize();
  }

  fabricInitialize() {
    let userDialog = document.querySelector(".userdialog");
    let dialog = userDialog.querySelector(".ms-Dialog");
    this.dialogComponent = new fabric['Dialog'](dialog);
  }

}



