import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { CommonService, Storage, AdminService, AuthenticationService, LoggerService } from '../../services';
import config from '../../config';
import { ISubscription } from "rxjs/Subscription";
import { Observable } from 'rxjs';
declare const Office: any;

@Component({
  selector: 'app-welcomepage',
  templateUrl: './welcomepage.component.html',
  styleUrls: ['./welcomepage.component.scss']
})
export class WelcomepageComponent implements OnInit, OnDestroy {

  language: any;
  authlink = config.api.AuthCode;
  public codeUnsubscribe: any;
  windowview: any;
  stopCondition: boolean = true;
  private subscription: ISubscription;
  private authsubscription: ISubscription;
  loading: boolean = false;
  userwindowview: any;
  dialogComponent: any;
  intervalIndex:number = 1;

  constructor(private router: Router,
    public commonService: CommonService,
    public userService: AdminService,
    public authenticationService: AuthenticationService,
    private storage: Storage,
    private logger: LoggerService
  ) {

  }

  ngOnInit() {
    console.log("wc component");
    // this.logger.log("Initializing wc component");
    this.loading = true;
    if (Office && Office.context && Office.context.mailbox) {
      console.log("wc component if");
      // this.logger.log("wc component if. welcomepageCpmt-line-44");
      let email = Office.context.mailbox.userProfile.emailAddress;
      this.authlink = this.authlink + email;
      this.authlink = this.authlink + config.api.redirecUrl;
      this.userService.getUserInfo(email).subscribe(data => {
        if (data) {
          if (data.email) {
            this.authsubscription=this.authenticationService.getauthresponse(data.email).subscribe(x => {

              this.loading = false;
              if (data.isActive) {
                this.storage.remove('user');
                data.accessToken = x.accessToken;
                data.expiresOn = x.expiresOn;
                data.extendedLifeTimeToken = x.extendedLifeTimeToken;

                this.storage.set('user', data);
                this.router.navigate([config.routes.action]);
              } else {
                this.router.navigate([config.routes.inactiveuser]);
              }
            });
          } else {
            this.loading = false;
            this.storage.remove('user');
            this.storage.set('user', data);
            this.language = this.commonService.getCurrentLanguage();
            // this.logger.log("reading auth code. welcomepageCpmt-line-71");
            this.readCode();
          }
        }
      }, error => {
        this.loading = false;
      });
    }
     else{
      console.log("wc component else");
      // this.logger.log("wc component else. welcomepageCpmt-line-80");
       let user = this.storage.get('user');
       if(!user){
         this.authlink = this.authlink + config.api.redirecUrl;
         this.userwindowview = window.open(this.authlink, "_blank", "width=400,height=400");
         this.setUserInStorage();
       }
      else{
         this.redirectToUserPage(user);
       }
     }
  }

  ngOnDestroy() {
    this.subscription && this.subscription.unsubscribe();
    this.authsubscription && this.authsubscription.unsubscribe();
  }

   continue() {
     this.windowview = window.open(this.authlink, "_blank", "width=300,height=300");
   }


  readCode() {
    var self:any = this;
    this.stopCondition = false;
    // this.logger.log("starting reading auth code interval. welcomepageCpmt-line-105");
    this.subscription = Observable.interval(2000)
      .takeWhile(() => !this.stopCondition)
      .subscribe(i => {
        // this.logger.log("parsing subscribtion result. Index: " + self.intervalIndex + ". welcomepageCpmt-line-109");
        let code = this.storage.get('code');
        if (code) {
          this.windowview.close();
          this.stopCondition = true;

          this.authenticationService.getauthwithuserresponse(code).subscribe(data => {

            this.storage.set('coderesponse', data);
            this.router.navigate([config.routes.registration]);
          });
        }
        self.intervalIndex ++;
      });
  }

  setUserInStorage() {
    this.stopCondition = false;
    this.subscription = Observable.interval(2000)
      .takeWhile(() => !this.stopCondition)
      .subscribe(i => {
        let code = this.storage.get('code');
        if (code) {
          this.userwindowview.close();
          this.stopCondition = true; 
          
          this.authenticationService.getauthwithuserresponse(code).subscribe(data => {
            this.storage.set('user', data.user);
            this.redirectToUserPage(data.user);
          });
        }
      });
  }
  redirectToUserPage(user){
    if(user.isActive && user.company.id && user.isSuperAdmin){
      this.router.navigate([config.routes.superuserhome]);
    }
    else if(user.isActive && user.company.id && user.type===true){
      this.router.navigate([config.routes.admin+'/'+user.company.id+'_'+user.type]);
    }
    else if(user.isActive && user.company.id && user.type===false){
      this.router.navigate([config.routes.admin+'/'+user.company.id+'_'+user.type+ '/root/' + user.id]);
    }
    else {
      this.dialogComponent.open();
    }
  }

}



