import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { HttpModule, Http, XHRBackend, RequestOptions, BaseRequestOptions } from '@angular/http';
import { RouterModule } from '@angular/router';
import { FileDropModule } from 'ngx-file-drop';
import { routes } from './app.routes';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { Ng2PaginationModule } from 'ng2-pagination';
import { NgMultiSelectDropDownModule } from 'ng-multiselect-dropdown';
import { NgPipesModule } from 'ngx-pipes';

import { AppComponent } from './app.component';
import { ApiBaseService } from './services/api-base.service';
import { IsAuthenticated, IsSuperAdminAuthenticated } from './services/app.routes-auth';
import {
  WelcomepageComponent,
  HomeComponent,
  ActionlistComponent,
  RegistrationComponent,
  SharefilesComponent,
  RequestdataComponent,
  RequestdataconsentComponent,
  RequestdatasubmitComponent,
  ConfirmationComponent,
  AdminuserComponent,
  AdminCompany,
  AuthComponent,
  OtpsendComponent,
  DownloadfileComponent,
  ViewfileComponent,
  DownloadpermissionfileComponent,
  PermissiongrantedComponent,
  HeaderComponent,
  AcceptComponent,
  RejectComponent,
  ShowpostdataComponent,
  AdminhomeComponent,
  AdminPackageComponent,
  AdminConsentComponent,
  LogsComponent,
  SuperuserComponent,
  SuperuserhomeComponent,
  NocontentComponent,
  BillingComponent,
  FoldersComponent,
  UserfolderComponent,
  UserfilesComponent,
  MessageinactiveuserComponent,
  RootfolderComponent,
  SharedfolderComponent,
  SharedfolderfileComponent,
  ManagesharingComponent,
  NewfolderComponent,
  FooterComponent,
} from './components';

import {
  CommonService,
  Storage,
  SharefileService,
  AdminService,
  AdminUserService,
  PostdataService,
  AuthenticationService,
  LoggerService
} from './services';


@NgModule({
  entryComponents: [
    NewfolderComponent,
    ManagesharingComponent
  ],
  declarations: [
    AppComponent,
    WelcomepageComponent,
    HomeComponent,
    ActionlistComponent,
    RegistrationComponent,
    SharefilesComponent,
    RequestdataComponent,
    RequestdataconsentComponent,
    RequestdatasubmitComponent,
    ConfirmationComponent,
    AdminuserComponent,
    AdminCompany,
    AuthComponent,
    OtpsendComponent,
    DownloadfileComponent,
    ViewfileComponent,
    DownloadpermissionfileComponent,
    PermissiongrantedComponent,
    HeaderComponent,
    AcceptComponent,
    RejectComponent,
    ShowpostdataComponent,
    AdminhomeComponent,
    AdminPackageComponent,
    AdminConsentComponent,
    LogsComponent,
    SuperuserComponent,
    NocontentComponent,
    SuperuserhomeComponent,
    BillingComponent,
    FoldersComponent,
    UserfolderComponent,
    UserfilesComponent,
    MessageinactiveuserComponent,
    RootfolderComponent,
    SharedfolderComponent,
    SharedfolderfileComponent,
    ManagesharingComponent,
    NewfolderComponent,
    FooterComponent
  ],
  imports: [
    BrowserModule,
    CommonModule,
    HttpClientModule,
    HttpModule,
    FormsModule,
    Ng2PaginationModule,
    FileDropModule,
    ReactiveFormsModule,
    NgPipesModule,
    NgbModule.forRoot(),
    NgMultiSelectDropDownModule.forRoot(),
    RouterModule.forRoot(routes, { useHash: true })
  ],
  providers: [
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    IsAuthenticated,
    IsSuperAdminAuthenticated,
    ApiBaseService,
    CommonService,
    AdminService,
    AdminUserService,
    Storage,
    PostdataService,
    AuthenticationService,
    LoggerService,
    SharefileService,
    {
      provide: ApiBaseService,
      deps: [XHRBackend, RequestOptions],
      useFactory: (Backend, defaultOptions) => new ApiBaseService(Backend, defaultOptions)
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



