import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { CommonService, Storage, AuthenticationService } from '../../services';
import { Location } from '@angular/common';
import { ISubscription } from "rxjs/Subscription";
import { Observable } from 'rxjs';
import config from '../../config';
declare const fabric: any;

@Component({
  selector: 'app-adminhome',
  templateUrl: './adminhome.component.html',
  styleUrls: ['./adminhome.component.scss']
})
export class AdminhomeComponent implements OnInit {

  private companyId = '';
  public usertype: any;
  selectmodule = 'user';
  navLinks = [];
  showGoBack=false;
  stopCondition: boolean = true;
  private subscription: ISubscription;
  windowview: any;
  dialogComponent: any;
  authlink = config.api.AuthCode;

  constructor(
    private router: Router,
    route: ActivatedRoute,
    public location: Location,
    private storage: Storage,
    private authenticationService: AuthenticationService
  ) {
    let keys = route.snapshot.params['id'].split('_');
    if (keys.length === 2) {
      this.companyId = keys[0];
      this.usertype = keys[1];
      this.storage.set('companyId', this.companyId);
      this.storage.set('idusertype', route.snapshot.params['id']);
      
    }
  }

  ngOnInit() {
    let route = this.location.path(true);
    if (route.includes('home')) {
      this.selectmodule = 'user';
      let user = this.storage.get('user');
      this.showGoBack = this.storage.get('user')?this.storage.get('user').isSuperAdmin:false;
      if (user && user.isActive && user.company.id && user.type!==null) {
        this.router.navigate([config.routes.admin+'/'+this.companyId+'_'+user.type]);
      } else {
        this.storage.remove('user');
        this.authlink = this.authlink + config.api.redirecUrl;
        this.readCode();
        this.windowview = window.open(this.authlink, "_blank", "width=400,height=400");
      }
    } else if (route.includes('log')) {
      this.selectmodule = 'logs';
    } else if (route.includes('folders') ||
      route.includes('root') ||
      route.includes('file') ||
      route.includes('userfolder') ||
      route.includes('sharedfolder') ||
      route.includes('sharedfolderfile')) {
      this.selectmodule = 'root';
    } else if (route.includes('consent')) {
      this.selectmodule = 'consent'
    } else if (route.includes('billing')) {
      this.selectmodule = 'billing';
    } else if (route.includes('company')) {
      this.selectmodule = 'settings';
    }
  }
  readCode() {
    this.stopCondition = false;
    this.subscription = Observable.interval(2000)
      .takeWhile(() => !this.stopCondition)
      .subscribe(i => {
        let code = this.storage.get('code');
        if (code) {
          this.windowview.close();
          this.stopCondition = true; 
          this.authenticationService.getauthwithuserresponse(code).subscribe(data => {

            data.user.type = true;
            this.storage.set('user', data.user);
            if (data.user.type && data.user.isActive) {
              this.router.navigate([config.routes.admin + '/' + data.user.company.id + '_' + data.user.type]);
            } else {
              this.dialogComponent.open();
            }
          });
        }
      });
  }

  routecheck(data) {
    this.selectmodule = data;
  }

}



